import * as React from "react";

import SEARCH from "feather-icons/dist/icons/search.svg";

const FEATHER = { SEARCH };

import "./icon.scss";

interface IIconProps {
    glyph: {
        content: string;
        viewBox: string;
        id: string;
        node: SVGSymbolElement;
    };
    preserveAspectRatio?: string;
    width?: number;
    height?: number;
}

export default class Icon extends React.Component<IIconProps, any> {
    render() {
        return (
            <svg
                className={`icon ${this.props.glyph.id}`}
                viewBox={this.props.glyph.viewBox}
                preserveAspectRatio={this.props.preserveAspectRatio}
                width={this.props.width}
                height={this.props.height}
            >
                <use xlinkHref={`#${this.props.glyph.id}`} />
            </svg>
        );
    }
}

export { FEATHER };